import React from 'react';
import ReactDom from 'react-dom';
import './index.css';
import Card from './Card';
import Sdata from "./Sdata";

console.log(Sdata[0]);
ReactDom.render(
<>
      <h1 className="heading_style"> List of top 5 Netflix series in 2022</h1>
     
      {Sdata.map((val) => {
            return (
                  <Card 
                        key ={val.id}
                        imgsrc={val.imgsrc}
                        title={val.title}
                        sname={val.sname}
                        link={val.link}
                  />
                  );
      })}
      
</>, document.getElementById('root'));


